
import XImg from 'vux/src/components/x-img/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
data () {
return {}
},
components: {
XImg,
XButton,
XHeader,
Tabbar,
TabbarItem
},
methods: {
clickMore() {
this.$router.push('/mydevice')
},
clickCheckDevice() {
this.$vux.loading.show()
var data = JSON.parse(localStorage.mydata)
var id = data.user.id
this.$http.get('http://localhost:8080/SmartHome/family/' + id, {}).then((res) => {
this.$store.commit('deviceInfo', res.body)
this.$router.push('/user_device')
}).catch((err) => {
this.$vux.alert.show({
content: '网络状态为：' + err.status
})
})
setTimeout(() => {
this.$vux.loading.hide()
}, 2000)
}
}
}



